.Signup-background {
  width: 100%;
  height: 700px;
  margin-top: 30px;
  background-image: url("../../plain_background_landing.jpg");
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: bottom center;
  background-size: cover;
  display: flex;
  justify-content: center;
  /* align-items: center; */
  padding-top: 120px;
}

.Signup-container {
  z-index: 10000;
}

.Signup-input {
  text-align: center;
  margin: 5px;
  height: 20px;
  word-wrap: break-word;
  font-family: "Roboto", sans-serif;
  width: 225px;
  border: none;
  align-content: center;
  border-bottom: 1px solid grey;
  margin-top: 10px;
  background: transparent;
}

.Profile-img-upload {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Profile-img-upload label {
  margin-bottom: 5px;
}

.Signup-button {
  border-radius: 50px;
  padding: 7px;
  color: white;
  background: #f94659;
  width: 133px;
  max-width: 100%;
  margin: 0 auto;
  margin-bottom: 40px;
  margin-top: 40px;
  font-size: 14px;
  font-weight: 300;
  border: none;
  box-shadow: 5px 5px 5px -5px rgb(0, 0, 0, 0.56);
  cursor: pointer;
}
