.Main-overview {
  width: 100%;
  height: 700px;
  position: top;
  margin-top: 60px;
  background-image: url("../../background_landing.jpg");
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-position: bottom center;
  background-size: cover;
}

.Main-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  text-align: left;
  height: 100%;
  width: 680px;
  margin: 0;
}

.Main-text h1 {
  font-size: 44px;
  line-height: 2.9rem;
  /* padding-bottom: 5px; */
  margin-left: 60px;
  margin-bottom: 0;
}

.Main-text p {
  font-size: 18px;
  line-height: 1.65rem;
  padding-bottom: 10px;
  color: #045da5;
  font-weight: 400;
}

.Main-sub-text {
  margin-left: 60px;
}

.Main-button-box {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Main-button {
  border-radius: 50px;
  padding: 13px;
  color: white;
  background: #f94659;
  width: 210px;
  max-width: 100%;
  margin: 0 auto;
  margin-bottom: 40px;
  margin-top: 40px;
  font-size: 18px;
  font-weight: 300;
  border: none;
  box-shadow: 5px 5px 5px -5px rgba(0, 0, 0, 0.56);
  cursor: pointer;
}

.Tech-icons {
  width: 100%;
  margin-left: 0;
  text-align: center;
}

@media (max-width: 1450px) {
  .Main-overview {
    background-image: url("../../plain_background_landing.jpg");
    display: flex;
    justify-content: center;
    height: 375px;
    padding-top: 75px;
  }
  .Main-text {
    justify-content: normal;
    text-align: center;
    align-items: center;
    padding-top: 55px;
    width: 605px;
    overflow: auto;
  }
  .Main-text h1 {
    font-size: 35px;
    margin: 0 15px 0 15px;
  }
  .Main-text p {
    font-size: 18px;
    margin: 0 15px 0 15px;
  }
  .Tech-icons {
    display: none;
  }
}

@media (max-width: 450px) {
  .Main-text {
    width: 307px;
  }
  .Main-text h1 {
    font-size: 21px;
    margin: 0 15px 0 15px;
    line-height: 24px;
    margin-bottom: 20px;
  }
  .Main-text p {
    font-size: 14px;
    margin: 0 15px 0 15px;
  }
}
